@import "reset.css";

body {
  height: 100%;
  background-color: #eff3f7;
}

html,
body,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.movies {
  width: 80%;
  display: grid;
  grid-template-columns: repeat(2, minmax(400px, 1fr));
  grid-gap: 100px;
}

@media (max-width: 1120px) {
  .movies {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
}
