@import "reset.css";

.movies__movie {
  background-color: white;
  height: 300px;
  margin: 80px auto 0;
  border-radius: 5px;
  box-shadow: 0px 6px 15px #2e305049;
  padding: 20px;
  flex-wrap: wrap;
}

.movie__data {
  overflow: hidden;
}

.movie__title {
  font-size: 25px;
  color: #000000;
}

.movie__year,
.movie__genres,
.movie__summary {
  color: #b8b8b8;
  margin-top: 10px;
}

.genres__genre {
  display: inline;
  margin-right: 10px;
}

.movie__summary {
  margin: 20px 0;
}

.movies__movie a {
  height: 100%;
  display: grid;
  grid-template-columns: minmax(150px, 1fr) 2fr;
  grid-gap: 20px;
}

.movie__poster {
  position: relative;
}

.movie__poster img {
  position: absolute;
  top: -50px;
  width: 150px;
}

@media (max-width: 1120px) {
  .movies__movie {
    width: 100%;
    height: 300px;
    flex-wrap: wrap;
    margin: 80px auto 0px;
  }
}
